/* FormCreator.css */

.form-creator-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .form-creator-heading {
    font-size: 20px;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .form-creator-input {
    margin-bottom: 15px;
  }
  
  .form-creator-label {
    display: block;
    margin-bottom: 5px;
  }
  
  .form-creator-select,
  .form-creator-input,
  .form-creator-button {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
  }
  
  .form-creator-options {
    margin-bottom: 15px;
  }
  
  .form-creator-option-input {
    margin-bottom: 5px;
  }
  
  .form-creator-option-button {
    display: block;
    margin-top: 10px;
  }
  
  .form-creator-preview {
    margin-top: 30px;
  }
  
  .form-creator-question {
    margin-bottom: 20px;
    border-bottom: 1px solid #ccc;
    padding-bottom: 20px;
  }
  
  .form-creator-question h3 {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .form-creator-file {
    margin-top: 10px;
  }
  
  .form-creator-file p {
    margin-bottom: 5px;
  }
  
  