.collapsible {
    margin-bottom: 10px;
}

.collapsible .collapsible-toggle {
    background-color: #f2f2f2;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 10px;
    width: 100%;
    text-align: left;
}

.collapsible .icon {
    float: right;
    transition: transform 0.3s ease;
}

.collapsible .icon.open {
    transform: rotate(-180deg);
}

.collapsible .collapsible-content {
    padding: 10px 20px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-top: none;
}
