.data-capture {
    padding: 20px;
}

.data-capture .table-container {
    margin-top: 20px;
}

.data-capture table {
    border-collapse: collapse;
    width: 100%;
}

.data-capture th, .data-capture td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

.data-capture th {
    background-color: #f2f2f2;
}

.data-capture input[type="text"] {
    width: 100%;
    padding: 5px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 4px;
    outline: none;
}

.data-capture button {
    margin-left: 10px;
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
    background-color: #4CAF50;
    color: white;
    cursor: pointer;
}

.data-capture button:hover {
    background-color: #d0954e;
}

.data-capture .data-title-header{
    font-size: 24px;
    font-weight: bold;
}
.data-capture .data-header{
    font-weight: bold;
}
