/* MilestonesPage.css */

.milestones-container {
  padding: 20px;
}

.milestones-container h2 {
  margin-bottom: 20px;
  font-size: 24px;
}

.milestone-form {
  margin-bottom: 20px;
}

.milestone-form input,
.milestone-form textarea,
.milestone-form select {
  width: 100%;
  margin-bottom: 10px;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.milestone-form button {
  background-color: #007bff;
  color: #fff;
  padding: 12px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}
.milestones-container .toggle-buttons button {
  background-color: #e7eaec;
  color: #151515;
  padding: 12px 20px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
  margin: 10px;
  margin-right: 15px;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border: 2px solid gray;
  box-shadow: 3px 3px 3px rgb(93, 101, 104);
}
.milestones-container button:hover {
  background-color: #0056b3;
}

.milestone-form .toggle-buttons button:hover {
  background-color: #a2bdd9;
  box-shadow: 1px 1px 1px rgb(10, 202, 255);
}

.scrollable-horizontal-user {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  width: 100%;
  margin-bottom: 10px;
}

.scrollable-horizontal-user li {
  margin-right: 10px;
}

.scrollable-horizontal-user label {
  display: flex;
  align-items: center;
}

.scrollable-horizontal-user input[type="checkbox"] {
  margin-right: 5px;
}

.milestone-tiles {
  display: flex;
  gap: 20px;
  max-width: 350px;
}

.milestone-tile {
  flex: 1 0 300px; /* Flex item to grow but not shrink below 300px */
  padding: 20px;
  background-color: #f0f0f0;
  border-radius: 8px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}

.milestone-tile:hover {
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.milestone-tile h3 {
  margin-bottom: 10px;
  font-size: 18px;
}

.milestone-tile p {
  margin-bottom: 5px;
  font-size: 16px;
  color: #555;
}
