.help-page {
    padding: 20px;
}

.help-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
}

.topic-dropdown {
    margin-bottom: 20px;
}

.topic-dropdown label {
    font-weight: bold;
    margin-right: 10px;
}

.topic-dropdown select {
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
    outline: none;
}

.help-content {
    line-height: 1.6;
}
