.user-details-nav-item{
    display: inline-block;
    margin-right: 10px;
  }
  
  .user-details-nav-item .user-link {
    text-decoration: none;
    color: #333;
    font-size: 16px;
    font-weight: bold;
    padding: 5px 10px;
    border-radius: 50%;
    background-color: #f0f0f0;
    transition: background-color 0.3s ease;
  }
  
 .user-details-nav-item .user-link:hover {
    background-color: #ccc;
  }
  