.header-tab-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #f0f0f0;
    padding: 10px;
    box-shadow: 2px 2px 4px grey;
    background: linear-gradient(to right, rgb(213, 211, 210), rgb(229, 229, 229), rgb(255, 255, 255));
  }
  
  .header-tab-bar .tab-left {
    flex: 1;
  }
  
  .header-tab-bar  .search-input {
    width: 60%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .header-tab-bar  .tab-right {
    display: flex;
  }
  
  .header-tab-bar  .tab-button {
    text-decoration: none;
    margin-left: 10px;
    padding: 8px 16px;
    border: none;
    border-radius: 5px;
    background-color: transparent;
    color: rgb(4, 4, 4);
    cursor: pointer;
    display: flex;
    align-items: center;
    transition: background-color 0.3s ease;
    box-shadow: 2px 2px 2px gray;
  }
  
  .header-tab-bar  .tab-button:hover {
    background-color: #a8afb6;
  }
  
  .header-tab-bar  .tab-button i {
    margin-right: 5px;
    color: rgb(53, 45, 30);
  }
  