.user-profile {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
  }
  
  .user-profile .user-details {
    margin-bottom: 10px;
  }
  
  .user-profile .edit-button {
    padding: 8px 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .user-profile .edit-button:hover {
    background-color: #0056b3;
  }
  