.dashboards-container {
    padding: 20px;
  }
  
  .dashboards-heading {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
  }

  .dashboards-heading span{
    font-size: 24px;
    font-weight: bold;
  }
  
  .create-dashboard-btn {
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border-radius: 5px;
    text-decoration: none;
    display: inline-block;
    margin-bottom: 20px;
  }
  
  .dashboard-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
  }
  
  .dashboard-tile-tile {
    background-color: #e0e0e0;
    padding: 20px;
    border-radius: 8px;
    text-decoration: none;
    color: #333;
    transition: background-color 0.3s ease;
  }
  
  .dashboard-tile-tile:hover {
    background-color: #c2bfbf;
    box-shadow: 2px 2px 2px gray;
  }
  
  .dashboard-tile-tile h3 {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .dashboard-tile-tile p {
    font-size: 16px;
    margin-bottom: 5px;
  }
  
  .dashboard-tile-tile i {
    font-style: italic;
  }
  

  /* Add this to your existing Dashboards.css file */

  .dashboards-container .filters {
  margin-bottom: 20px; /* Adjust spacing as needed */
}

.dashboards-container .filters input[type='text'] {
  width: 100%; /* Take up full width of its container */
  padding: 10px; /* Adjust padding as needed */
  font-size: 16px; /* Adjust font size as needed */
  border: 1px solid #ccc; /* Add border */
  border-radius: 5px; /* Add border radius for rounded corners */
  box-sizing: border-box; /* Include padding and border in the width */
}

.dashboards-container .filters input[type='text']:focus {
  outline: none; /* Remove default focus outline */
  border-color: #007bff; /* Change border color when focused */
}
