.workspace-form-container {
    width: 80%;
    max-width: 400px;
    margin: 20px auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
  }
   .workspace-form-container h2 {
    margin-bottom: 20px;
    font-size: 1.5rem;
  }
  
 .workspace-form-container .input-group {
    margin-bottom: 15px;
  }
  
 .workspace-form-container .input-group label {
    display: block;
    margin-bottom: 5px;
  }
  
 .workspace-form-container .input-group input[type='text'],
 .workspace-form-container .input-group textarea {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
 .workspace-form-container .status-message {
    margin-top: 10px;
    color: #ff0000;
  }
  
  .workspace-form-container button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .workspace-form-container button:hover {
    background-color: #0056b3;
  }
  