.login-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    max-width: 1000px;
    margin: auto;
  }
  
  .login-container .side-image {
    flex: 1;
  }
  .login-container  .horizontal-content{
    display: flex;
    flex-direction: row;
  }
  
  .login-container .side-image img {
    max-width: 500px;
    height: auto;
  }
  
  .login-container .login-form {
    flex: 1;
    max-width: 400px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #fff;
  }
  
  .login-container .login-form input {
    width: 80%;
    margin-bottom: 15px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .login-container .message-text {
    font-size: 14px;
    margin-bottom: 15px;
  }
  
  .login-container .message-text.success {
    color: green;
  }
  
  .login-container .message-text.error {
    color: red;
  }
  
  .login-container button {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .login-container button:hover {
    background-color: #0056b3;
  }
  