.dashboard-container {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  
  .dashboard-header {
    background-color: transparent;
    color: #0b0a0a;
    padding: 10px;
    text-align: center;
  }
  
  .routeback a {
    color: #486bbc;
    text-decoration: none;
  }
  .routeback link {
    color: #486bbc;
    text-decoration: none;
  }
  
  .dashboard-sidebar {
    flex: 1;
    background-color: #f0f0f0;
    padding: 20px;
  }
  
  .dashboard-content {
    flex: 3;
    padding: 20px;
  }
  
  .dashboard-content .routeback {
    margin: 20px;
    display: block;
  }

  .dashboard-body{
    display: flex;
  }
  

  @media (max-width: 768px) {
    .dashboard-body{
        display: inline-block;
        flex-direction: column;
      }
    .dashboard-sidebar {
        flex: 1;
        background-color: #f0f0f0;
        padding: 1px;
      }
}