.updates-container {
  padding: 20px;
  width: 70%;
  max-height: 400px;
  overflow-y: auto;
  margin: auto;
}

.update-item {
  background-color: #c3c3c3;
  border-radius: 10px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

.update-item:last-child {
  margin-bottom: 0;
}

.update-header {
  background-color: #c0c0c0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  padding: 10px;
}

.update-header span {
  font-size: 14px;
  color: #666;
}

.notification-section {
  margin-bottom: 20px;
}

.notification-input {
  width: 100%;
  margin-bottom: 10px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.user-selection {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.user-search-input {
  width: 100%;
  margin-bottom: 10px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.scrollable-horizontal-user {
  width: 100%;
  max-height: 150px;
  overflow-y: auto;
  list-style: none;
  padding: 0;
  margin: 0;
}

.scrollable-horizontal-user li {
  margin-bottom: 5px;
}

.scrollable-horizontal-user li label {
  display: flex;
  align-items: center;
}

.scrollable-horizontal-user li label input[type="checkbox"] {
  margin-right: 5px;
}
