/* AddEditForm.css */

.add-edit-form .modal {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.4);
  }
  
  .add-edit-form .modal-content {
    background-color: #fefefe;
    margin: 15% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 50%;
  }
  
  .add-edit-form  .close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
  }
  
  .add-edit-form .close:hover,
  .add-edit-form .close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
  }
  
  .add-edit-form  .form-group {
    margin-bottom: 20px;
  }
  
  .add-edit-form  button {
    background-color: #4caf50;
    color: white;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .add-edit-form button:hover {
    background-color: #45a049;
  }
  