.workspace-option-pills {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #f0f0f0;
    padding: 10px 0;
  }
  
  .workspace-option-pill {
    cursor: pointer;
    padding: 10px;
    border-radius: 5px;
    transition: background-color 0.3s;
  }
  
  .workspace-option-pill.selected {
    background-color: #ffad50;
    color: #fff;
  }
  