/* ResourcesPage.css */
.resources-container {
    padding: 20px;
  }
  
  .resources-container h2 {
    margin-bottom: 20px;
  }
  
  .resources-container .file-upload {
    margin-bottom: 20px;
    display: flex;
    align-items: end;
    justify-content: flex-end;
  }
  
  .resources-container .file-upload input[type='file'] {
    margin-right: 10px;
  }
  
  .resources-container .file-upload input[type='text'] {
    flex: 1;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 400px;
  }
  
  .resources-container .file-upload button {
    background-color: #007bff;
    color: #fff;
    padding: 8px 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .resources-container .resources-list {
    margin-bottom: 20px;
  }
  
  .resources-container .resources-list table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .resources-container .resources-list th,
  .resources-container .resources-list td {
    padding: 10px;
    border: 1px solid #ccc;
  }
  
  .resources-container .resources-list th {
    background-color: #f0f0f0;
  }
  
  .resources-container .resources-list a {
    text-decoration: none;
    color: #333;
  }
  
  .resources-container .resources-list a:hover {
    text-decoration: underline;
  }
  