.header {
    background-color: #333;
    color: #fff;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .uhalisi-logo img {
    width: 40px;
    height: auto;
    margin-right: 10px;
  }
  
  .logo-text {
    font-size: 20px;
    font-weight: bold;
  }
  
  .logo-link {
    text-decoration: none;
    color: #fff;
  }
  
  .user-details-container {
    display: flex;
    align-items: center;
  }
  
  .user-details-container .logout-link {
    color: #fff;
    text-decoration: none;
    padding: 8px 16px;
    border-radius: 20px;
    background-color: #ed8c31;
    transition: background-color 0.3s ease;
    margin: 10px;
  }
  
  .user-details-container .logout-link:hover {
    background-color: #0056b3;
  }
  
  .user-details-container .user-details {
    background-color: #007bff;
    color: #fff;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
  }
  