.dashboard-content {
    padding: 20px;
  }
  
  .dashboard-content .tab-pills {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
    
  }
  
  .dashboard-content  .tab-pills span {
    padding: 10px 20px;
    border: 1px solid #ccc;
    cursor: pointer;
    box-shadow: 1px 1px 1px gray;
  }
  
  .dashboard-content .tab-pills span:hover {
    background-color: #f0f0f0;
  }
  
  .dashboard-content .contentPanel{
      border-top: 2px ridge;
      border-left: 2px ridge;
  }