.dashboard-form-container {
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
  }
  
 .dashboard-form-container .dashboard-form-container h2 {
    margin-bottom: 15px;
  }
  
 .dashboard-form-container .input-group {
    margin-bottom: 15px;
  }
  
 .dashboard-form-container .input-group label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
 .dashboard-form-container .input-group input,
 .dashboard-form-container .input-group textarea {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
  }
  
 .dashboard-form-container .input-group textarea {
    height: 100px;
    resize: none;
  }
  
  .dashboard-form-container button {
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .dashboard-form-container  button:hover {
    background-color: #0056b3;
  }
  