.workspaces-container {
    padding: 20px;
  }
  
  .workspaces-heading {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
  }
  
  .workspaces-heading span {
    font-size: 24px;
    font-weight: bold;
  }
  
  .create-workspace-btn {
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border-radius: 5px;
    text-decoration: none;
    display: inline-block;
    margin-bottom: 20px;
  }
  
  .workspace-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
  }
  
  .workspace-tile {
    background-color: #e0e0e0;
    padding: 20px;
    border-radius: 8px;
    text-decoration: none;
    color: #333;
    transition: background-color 0.3s ease;
  }
  
  .workspace-tile:hover {
    background-color: #c2bfbf;
    box-shadow: 2px 2px 2px gray;
  }
  
  .workspace-tile h3 {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .workspace-tile p {
    font-size: 16px;
    margin-bottom: 5px;
  }
  
  .workspace-tile i {
    font-style: italic;
  }
  
  /* Additional styles for search filter */
  .workspaces-container .filters {
    margin-bottom: 20px;
  }
  
  .workspaces-container .filters input[type='text'] {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
  }
  
  .workspaces-container .filters input[type='text']:focus {
    outline: none;
    border-color: #007bff;
  }
  