.welcome-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f0f0f0; /* Adjust background color as needed */
}

.welcome-content {
    text-align: center;
    margin-bottom: 20px;
}

.welcome-container .page-button {
    padding: 10px 20px;
    background-color: #007bff; /* Button background color */
    color: #fff; /* Button text color */
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.welcome-container .page-button:hover {
    background-color: #0056b3; /* Button hover background color */
}

.welcome-container .login-card,
.welcome-container .register-card {
    width: 600px; /* Adjust card width as needed */
    padding: 20px;
    background-color: #fff; /* Card background color */
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Card shadow */
    margin-top: 20px;
}
