/* CompanyDataView.css */

.company-data-view {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
  }
  
  .company-data-view h2 {
    margin-bottom: 20px;
    font-size: 24px;
    color: #333;
  }
  
  
.company-data-view .section-dropdown {
    margin-bottom: 20px;
  }
  
  .section-dropdown label {
    font-weight: bold;
    margin-right: 10px;
  }
  
  .section-dropdown select {
    padding: 8px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  

  .company-data-view table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
  }
  
  
.company-data-view table th,
  
.company-data-view table td {
    padding: 10px;
    border: 1px solid #ccc;
  }
  
  
.company-data-view table th {
    background-color: #f0f0f0;
    font-weight: bold;
    text-align: left;
  }
  
 
.company-data-view table td {
    text-align: left;
  }
  
 
.company-data-view button {
    padding: 8px 16px;
    margin-right: 10px;
    border: none;
    border-radius: 4px;
    background-color: #007bff;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
  }
  
 
.company-data-view button:hover {
    background-color: #0056b3;
  }
  
  