.sidebar-container {
  height: 100%;
  overflow-y: auto; /* Vertical scrolling on larger screens */
  overflow-x: hidden; /* Hide horizontal overflow */
  padding: 10px;
}

.nav {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.nav-item {
  padding: 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: background-color 0.3s ease;
}

.nav-item i {
  margin-right: 10px;
}

.nav-item:hover {
  background-color: #555;
}

/* Active tab styles */
.nav-item.active {
  background-color: #cccbcb;
  box-shadow: 2px 2px 2px inset gray;
}

/* More button */
.more-item {
  padding: 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 2px 2px 2px rgb(82, 82, 82);
  background-color: #cfcfcf;
}

/* Modal styles */
.modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  max-width: 400px;
  border-radius: 8px;
}

.modal-item {
  padding: 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: background-color 0.3s ease;
}

.modal-item:hover {
  background-color: #555;
}

/* Responsive styles */
@media (max-width: 768px) {
  .sidebar-container {
    height: auto; /* Reset height */
    overflow-x: auto; /* Horizontal scrolling on mobile */
    overflow-y: hidden; /* Hide vertical overflow */
    display: flex;
    flex-wrap: nowrap;
  }

  .nav {
    flex: 1;
    display: flex;
    flex-direction: row;
  }

  .nav-item {
    box-shadow: 2px 2px 2px inset gray;
    margin: 2px;
  }

  /* Hide More button on mobile */
  .more-item {
    display: none;
  }
}
